import React from 'react'
import './footer.css'

export default function Footer() {
  function renderLinks() {
    return [
      { name: 'GitHub', url: 'https://github.com/Nickdem' },
      { name: 'Резюме', url: 'https://hh.ru/resume/0f6f14a7ff06e402860039ed1f6b5938363269' }
    ].map((item) => {
      return (
        <a
          key={item.name}
          href={item.url}
          target='_blank'
          rel='noreferrer'
        >
          {item.name}
        </a>
      )
    })
  }

  return (
    <footer className='footer'>
      <div className='footer__links'>
        {renderLinks()}
      </div>
    </footer>
  )
}