import React, { useState } from 'react'
import iam from '../../img/iam.jpg'
import './home.css'

export default function Home() {
  const [text, setText] = useState('Меня зовут Никита Демидов и я молодой junior front-end разработчик')

  function randomInteger(min, max) {
    let rand = min + Math.random() * (max + 1 - min);
    return Math.floor(rand);
  }

  const reactItem = ['R', 'e', 'a', 'c', 't'].map((item, idx) => <span key={idx} className='r-it'><span  className='r-symbol'>{item}</span></span>)

  const vueItem = ['V', 'u', 'e'].map((item, idx) => <span style={{transition: `linear ${(idx + 1) * 400}ms`}} className={'v-symbol'} key={idx}>{item}</span>)

  const angularItem = ['A', 'n', 'g', 'u', 'l', 'a', 'r'].map((item, idx) => <span className='a-symbol' onMouseLeave={(e) => e.target.style.textShadow = ''} onMouseEnter={(e) => e.target.style.textShadow = `${((idx % 2) === 0) ? '-' : ''}${randomInteger(3, 10)}px ${((idx % 2) === 0) ? '' : '-'}${randomInteger(10, 15)}px 0px #ff4e4e`} key={idx}>{item}</span>)

  return (
    <main>
      <div className='home-top'>
        <div className='home-top__left'>
          <img className='home-top__img' src={iam} alt='Никита Демидов' />
        </div>
        <div className='home-top__right'>
          <h1 className='home-top__title'>Доброго времени суток!</h1>
          <p
            className='home-top__text'
            onMouseEnter={() => setText('Опыт работы мал, но имеется большое стремление изучать новое')}
            onMouseLeave={() => setText('Перейдите на страницу проектов, там может быть что-то интересное')}
          >
            {text}
          </p>
        </div>
      </div>
      <div className='home-bottom'>
        <h2 className='home-bottom__title'>Кратко обо мне</h2>
        <p className='home-bottom__text'>
          Прошёл обучение в HTML Academy на Frontend-разработчика.
          Работал с {reactItem}.
          Знаком с <span className='a-it'>{angularItem}</span> и <span className='v-it'>{vueItem}</span>.
          Увлекаюсь резиновой, адаптивной кроссбраузерной вёрсткой.
        </p>
      </div>
    </main>
  )
}