import React from 'react'

export default function Polaroid({ item, cls, clickHandler, idx }) {

  return (
    <figure
      onClick={() => clickHandler(idx - 1)}
      className={cls}
      style={{ background: idx && item.active ? '#b3a577' : '#ffffff' }}
    >
      <img src={item.img} alt={item.desc} />
      <figcaption>{item.desc}</figcaption>
    </figure>
  )
}

Polaroid.defaultProps = {
  clickHandler() { }
}