import React, { useEffect, useState } from 'react'
import first from '../../img/first.png'
import second from '../../img/second.png'
import third from '../../img/third.png'
import rea from '../../img/react.png'
import ng from '../../img/ng.png'
import progress from '../../img/progres.jpg'
import winter from '../../img/winter.jpg'
import volga from '../../img/volga.jpg'
import msk from '../../img/msk.jpg'
import './about.css'
import Polaroid from '../../components/polaroid'
import Sertificates from '../../components/sertificates'

export default function About() {
  const [data, setData] = useState({
    current: null,
    poraloids: [
      { desc: 'Процесс создания портфолио', img: progress },
      { desc: 'Прекрасный вид на Волгу', img: volga },
      { desc: 'Бесконечная зима', img: winter },
      { desc: 'Любовь к истории 20 века', img: msk }
    ]
  })

  const sertificates = [
    { url: 'https://assets.htmlacademy.ru/certificates/intensive/89/903011.pdf?1548154615', img: first, desc: 'Сертификат 1 уровень html и css' },
    { url: 'https://udemy-certificate.s3.amazonaws.com/pdf/UC-HHPXGIYR.pdf', img: rea, desc: 'Сертификат об окончании курса по react' },
    { url: 'https://assets.htmlacademy.ru/certificates/intensive/119/903011.pdf?1564919924', img: third, desc: 'Сертификат 1 уровень js' },
    { url: 'https://udemy-certificate.s3.amazonaws.com/pdf/UC-63411ee8-6c0d-48c6-a09b-fb05a8f1782a.pdf', img: ng, desc: 'Сертификат об окончании курса по angular' },
    { url: 'https://assets.htmlacademy.ru/certificates/intensive/113/903011.pdf?1561655297', img: second, desc: 'Сертификат 2 уровень html и css' }
  ]

  useEffect(() => {
    currentPoraloid(0)
    //eslint-disable-next-line
  }, [])

  function currentPoraloid(id) {
    if (data.poraloids[id].active === true) return
    setData(prev => {
      prev.poraloids.forEach(item => item.active = false)
      prev.poraloids[id].active = true
      return { poraloids: [...prev.poraloids], current: prev.poraloids[id] }
    })
  }

  return (
    <main>
      <div className='about-top'>
        <div>
          {data.current &&
            <Polaroid item={data.current} cls='about-top__polaroid' />
          }
          <div className='about-top__photos'>
            {
              data.poraloids.map((poraloid, i) => {
                return (
                  <Polaroid key={i} clickHandler={currentPoraloid} idx={i+1} item={poraloid} cls='about-top__photo' />
                )
              })
            }
          </div>
        </div>
        <p className='about-text'>
          Меня зовут, Никита. Я живу в городе Чебоксары.
          Стажировался React-разработчиком в нескольких компаниях.
          Занимался созданием видеосервиса с рендерингом на серверной стороне(NextJS),
          а так же внутренними проектами(ERP).
          Это помогло закрепить знания о React на хуках и React+Redux. Решив не останавливаться я начал изучать Angular и Vue.
          Прошёл обучение в HTML Academy на Frontend-разработчика(HTML, CSS, JavaScript). Занимаюсь резиновой и адаптивной вёрсткой.
          Участвовал в "Хакатон НацПроектов в Чувашии".
        </p>
      </div>
      <div className='sertificates'>
        {sertificates.map((sert, id) => {
          return (
            <Sertificates key={id} item={sert} />
          )
        })}
      </div>
      <h2 className='about-title'>Самый удобный способ связи: <span>nickdem559@inbox.ru</span></h2>
    </main>
  )
}