import React from 'react'
import Footer from '../footer'
import Header from '../header'
import './layout.css'

export default function Layout({ children }) {
  return (
    <div className='layout'>
      <Header />
      {children}
      <Footer />
      <div className='beta'>
        <h2>
          [Beta version]
        </h2>
      </div>
    </div>
  )
}