import React, { useState } from 'react'
import Card from '../../components/card'
import './projects.css'

export default function Projects() {
  const [btn, setBtn] = useState('Фреймворки')

  const data = [
    {
      title: 'Вёрстка/JS',
      projects: [
        { name: 'Экскурсии', desc: 'Адаптивная резиновая вёрстка одной из страниц сайта по проведению экскурсий. Используется сборщик Gulp и css-препроцессор.', url: 'https://github.com/Nickdem/test-excursion', type: 'HTML/CSS' },
        { name: 'Scores', desc: 'Игра напоминающая кости, необходимо набрать нужное количество очков(100, можно поменять в специальном поле).', url: 'https://github.com/Nickdem/game-scores', host: 'https://nickdem.github.io/score/', type: 'JS' },
        { name: '2048', desc: 'Игра 2048. Игровое поле имеет форму квадрата 4x4. Целью игры является получение плитки номинала «2048». Ожидает изменения скриптов.', url: 'https://github.com/Nickdem/2048-game', host: 'https://nickdem.github.io/2048/', type: 'JS' },
        { name: 'CatEnergy', desc: 'Учебный проект. Адаптивная вёрстка многостраничного интернет-магазина полезного питания для котов. Используется сборщик Gulp и css-препроцессор.', url: 'https://github.com/Nickdem/903011-cat-energy-16', type: 'HTML/CSS' },
        { name: 'Kekstagram', desc: 'Учебный проект. Сервис для просмотра фотографий и комментариев, присутствует подобие фотофильтра, есть возможность группировки фотографий.', url: 'https://github.com/Nickdem/903011-kekstagram-17', type: 'JS' },
        { name: 'Sedona', desc: 'Самый первый учебный проект. Статичная вёрстка двух страниц связанных с небольшим, но классным городом в Аризоне.', url: 'https://github.com/Nickdem/903011-sedona', type: 'HTML/CSS' },
        { name: 'JS -Конструктор', desc: 'Простенький конструктор для создания блоков страницы. В боковом меню имеются поля для заполнения нужным контентом и стилями.', url: 'https://github.com/Nickdem/js-constructor', type: 'JS' },
        { name: 'Закрытие предприятий', desc: 'Адаптивная резиновая вёрстка главной страницы сайта по комплексному закрытию предприятий', url: 'https://github.com/Nickdem/complex-closure', type: 'HTML/CSS' }
      ]
    },
    {
      title: 'Фреймворки',
      projects: [
        { name: 'Викторина', desc: 'Обычная викторина с возможностью проходить тесты, а для создания теста требуется авторизация. При входе/создании аккаунта используйте ненастоящие почту и пароль.', url: 'https://github.com/Nickdem/react-quiz', host: 'https://react-quiz-a3e89.firebaseapp.com/', type: 'React' },
        { name: 'CRM', desc: 'CRM-система для работы со своим бюджетом. При входе/создании аккаунта используйте ненастоящие почту и пароль.', url: 'https://github.com/Nickdem/vue-crm', host: 'https://vue-crm-48883.firebaseapp.com/', type: 'Vue' },
        { name: 'Блог', desc: 'Простой блог с админкой по авторизации. В админке можно создать/обновить/удалить посты.', url: 'https://github.com/Nickdem/angular-blog', host: 'https://angular-blog-56543.firebaseapp.com/', type: 'Angular' },
        { name: 'Pokemons', desc: 'Покемоны из вселенной Pocket Monsters.', url: 'https://github.com/Nickdem/test-pokemon', host: 'https://nickdem.github.io/pokemons-site/', type: 'React' },
        { name: 'Star Wars', desc: 'Корабли, персонажи и планеты из вселенной Star Wars.', url: 'https://github.com/Nickdem/starwars', type: 'React' },
        { name: 'Giphy', desc: 'Получение рандомных гифок по введенному в поле запросу. Есть группировка по названию запроса.', url: 'https://github.com/Nickdem/starwars', type: 'React' },
        { name: 'Simon', desc: 'Саймон - игра, направленная на развитие и тренировку памяти и скорости реакции.', url: 'https://github.com/Nickdem/test-simon-game', host: 'https://nickdem.github.io/simon-game/', type: 'Vue' },
        { name: 'Github-Search', desc: 'Поиск репозиториев и пользователей на гитхаб', url: 'https://github.com/Nickdem/react-hooks', type: 'React' },
        { name: 'Органайзер', desc: 'Календарь с возможностью записывать задачи на какой-либо день.', url: 'https://github.com/Nickdem/Calendar-organizer', type: 'Angular' },
        { name: 'Sport-center', desc: 'Сайт спорт-центра. Первые попытки работы с Реакт.', url: 'https://github.com/Nickdem/sport-center-level', type: 'React' },
        { name: 'Хакатон', desc: 'Проект для "ОНЛАЙН ХАКАТОНА НАЦПРОЕКТЫ ЧУВАШИИ".', url: 'https://github.com/Nickdem/hackaton-project', type: 'React' },
        { name: 'ToDo', desc: 'Лист задач с календарем и временем.', url: 'https://github.com/Nickdem/vue-task-list', type: 'Vue' }
      ]
    }
  ]

  function renderPrjects(d) {
    return (
      d.projects.map(project =>
        <Card key={project.name} item={project} />
      )
    )
  }

  function renderButtons(names) {
    return names.map(name => <button className='projects__button' key={name} onClick={() => setBtn(name)} disabled={btn === name}>{name}</button>)
  } 

  return (
    <main>
      {data &&
        <div>
          <div className='projects__buttons'>
            {renderButtons(data.map(item => item.title))}
          </div>
          <div className='projects__list'>
            {renderPrjects(data.find(d => d.title === btn))}
          </div>
        </div>
      }
    </main>
  )
}