import React from 'react'

export default function Card({ item }) {

  return (
    <div className='projects__item' key={item.name}>
      <h3>{item.name}</h3>
      <p>{item.desc}</p>
      <div className='project__buttons'>
        <a href={item.url} target='_blank' rel='noopener noreferrer'>код</a>
        {item.host && <a href={item.host} target='_blank' rel='noopener noreferrer'>открыть</a>}
      </div>
      <span>{item.type}</span>
    </div>
  )
}